"use client";

import React, { useEffect, useState } from 'react';

// Define the types for the SearchPaxContent props
interface SearchPaxContentProps {
  searchData: {
    paxName: string;
    passportNo: string;
    referenceNo: string;
  };
  setSearchData: React.Dispatch<React.SetStateAction<{
    paxName: string;
    passportNo: string;
    referenceNo: string;
  }>>;
  handleChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  handleSearch: (e: React.FormEvent) => Promise<void>;
  handleClear: () => void;
  isSearching: boolean;
  searchResults?: {
    passengers_info: PassengerInfo[];
  };
}

// Add the new interface for passenger info
interface PassengerInfo {
  id: number;
  first_name: string;
  last_name: string;
  email: string;
  dob: string;
  passport_number: string;
  address_line_1: string;
  address_line_2: string;
  city: number;
  state: number;
  country: number;
  zip: string;
  occupation: string;
}

// Component for Search Pax tab
const SearchPaxContent: React.FC<SearchPaxContentProps> = ({
  searchData,
  setSearchData,
  handleChange,
  handleSearch,
  handleClear,
  isSearching,
  searchResults
}) => {
  const [referenceNumber, setReferenceNumber] = useState<string>('');
  
  // Only access localStorage on the client side
  useEffect(() => {
    if (typeof window !== 'undefined') {
      setReferenceNumber(localStorage.getItem('referenceNumber') || '');
    }
  }, []);

  // Format date from ISO string to YYYY-MM-DD
  const formatDate = (dateString: string) => {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toISOString().split('T')[0];
  };

  // Handle using a passenger (will be implemented later)
  const handleUsePassenger = (passengerId: number) => {
    console.log(`Using passenger with ID: ${passengerId}`);
    // Functionality to be added later
  };

  return (
    <>
      {/* Form Container */}
      <div className="mx-6 mt-[21px] mb-6 border-[1.5px] border-[#E6EAF2] rounded-2xl">
        {/* Reference No Header with gray background */}
        <div className="bg-[#F6F7F9] py-4 px-6 border-b border-gray-200 rounded-t-2xl">
          <p className="text-base font-medium text-[#1C1C1C]">{`Reference No: ${referenceNumber}`}</p>
        </div>
        
        {/* Form */}
        <div className="p-6">
          <form onSubmit={handleSearch} className="space-y-8">
            <div className="grid grid-cols-5 gap-8">
              <div>
                <label htmlFor="paxName" className="block text-sm font-medium text-gray-700 mb-2">
                  PAX Name
                </label>
                <input
                  type="text"
                  id="paxName"
                  name="paxName"
                  value={searchData.paxName}
                  onChange={handleChange}
                  placeholder=""
                  className="w-full px-3 py-2.5 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-[#0B498B] text-[#6A6A6A]"
                />
              </div>
              
              <div>
                <label htmlFor="passportNo" className="block text-sm font-medium text-gray-700 mb-2">
                  Passport No<span className="text-red-500 ml-0.5">*</span>
                </label>
                <input
                  type="text"
                  id="passportNo"
                  name="passportNo"
                  value={searchData.passportNo}
                  onChange={handleChange}
                  placeholder=""
                  className="w-full px-3 py-2.5 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-[#0B498B] text-[#6A6A6A]"
                />
              </div>
              
              <div>
                <label htmlFor="referenceNo" className="block text-sm font-medium text-gray-700 mb-2">
                  Reference No<span className="text-red-500 ml-0.5">*</span>
                </label>
                <div className="flex items-center">
                  <input
                    type="text"
                    id="referenceNo"
                    name="referenceNo"
                    value={searchData.referenceNo}
                    onChange={handleChange}
                    placeholder=""
                    className="w-full px-3 py-2.5 border border-gray-300 rounded-md focus:outline-none focus:ring-1 focus:ring-[#0B498B] text-[#6A6A6A]"
                  />
                  <button
                    type="button"
                    onClick={handleClear}
                    className="ml-3 text-sm text-[#0B498B] font-semibold hover:underline"
                  >
                    Clear
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
      
      {/* Search Button Container */}
      <div className="flex justify-end p-6 pt-0">
        <button
          onClick={handleSearch}
          disabled={isSearching}
          className="bg-[#0B498B] text-white px-8 py-2 rounded-md hover:bg-[#083968] transition-colors focus:outline-none focus:ring-2 focus:ring-[#0B498B] focus:ring-opacity-50 disabled:opacity-70 disabled:cursor-not-allowed font-medium"
        >
          {isSearching ? 'Searching...' : 'Search'}
        </button>
      </div>

      {/* Passenger Search Results */}
      {searchResults && searchResults.passengers_info && searchResults.passengers_info.length > 0 && (
        <div className="mx-6 mb-6 border-[1.5px] border-[#E6EAF2] rounded-2xl">
          <div className="bg-[#F6F7F9] py-4 px-6 border-b border-gray-200 rounded-t-2xl">
            <p className="text-base font-medium text-[#1C1C1C]">Search Results</p>
          </div>
          
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Full Name
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Email
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Date of Birth
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Passport Number
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Address
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Occupation
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {searchResults.passengers_info.map((passenger) => (
                  <tr key={passenger.id}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-700">
                      {`${passenger.first_name} ${passenger.last_name}`}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-700">
                      {passenger.email}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-700">
                      {formatDate(passenger.dob)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-700">
                      {passenger.passport_number}
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-700">
                      <div>
                        {`${passenger.address_line_1}${passenger.address_line_2 ? ', ' + passenger.address_line_2 : ''}`}
                      </div>
                      <div>
                        {`${passenger.city}, ${passenger.state}, ${passenger.country}, ${passenger.zip}`}
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-700">
                      {passenger.occupation}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm">
                      <button
                        onClick={() => handleUsePassenger(passenger.id)}
                        className="bg-[#0B498B] text-white px-4 py-1.5 rounded-md hover:bg-[#083968] transition-colors focus:outline-none focus:ring-1 focus:ring-[#0B498B] text-sm"
                      >
                        Use
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </>
  );
};

export default React.memo(SearchPaxContent);